import { TipoStatusOJ } from './../../../model/objetc/tipo-status-oj';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Base } from '../../../model/base';
import { StatusItemInspecao } from '../../../model/objetc/statusiteminspecao';
import { StatusitensinspecaoService } from './../../../service/sigobra/statusitensinspecao.service';
@Component({
  selector: 'app-statusitensinspecao-inserir',
  templateUrl: './statusitensinspecao-inserir.page.html',
  styleUrls: ['./statusitensinspecao-inserir.page.scss'],
})
export class StatusitensinspecaoInserirPage implements OnInit {

  status: StatusItemInspecao = new StatusItemInspecao();
  listaTipoStatusOJ: Array<TipoStatusOJ> = new Array<TipoStatusOJ>();
  constructor(private base: Base, private siis: StatusitensinspecaoService, private router: Router) {
    this.listaTipoStatusOJ = siis.getListStatus();
    this.carregarCores();
  }

  cores = [];
  ngOnInit() {
    if ((this.siis.status !== undefined) && (this.siis.status !== null)) {
      this.status = this.siis.status;
    }
  }

  get editando() {
    return Boolean(this.status.id)
  }

  salvar() {
    this.salvando();
  }

  salvando() {

    if ((this.status.ativo === undefined) || (this.status.ativo === null)) {
      this.status.ativo = true;
    }
    this.base.present();
    this.siis.salvar(this.status).subscribe(
      data => {
        this.siis.status = new StatusItemInspecao();
        this.base.dismiss();
        this.router.navigate(['statusitensinspecoes']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao salvar status :' + this.base.tratarErro(error));
      }
    );
  }

  keyDownFunction(event: any) {
    if (event.keyCode === 13) {
      this.salvando();
    }
  }

  tipoStatusChange(event: any) {
    this.status.tipoStatus = event.item.tipoStatus;
  }

  carregarCores() {
    const r = new Array("00", "33", "66", "99", "CC", "FF");
    const g = new Array("00", "33", "66", "99", "CC", "FF");
    const b = new Array("00", "33", "66", "99", "CC", "FF");

    for (let i = 0; i < r.length; i++) {
      for (let j = 0; j < g.length; j++) {
        for (let k = 0; k < b.length; k++) {
          const novoc = "#" + r[i] + g[j] + b[k];
          this.cores.push(novoc);
        }
      }
    }
  }

  corChange(event: any) {
    this.status.cor = event.item.nome;
  }


  prepareColorSelector() {
    setTimeout(() => {
      let buttonElements = document.querySelectorAll('div.alert-radio-group button');
      if (!buttonElements.length) {
        this.prepareColorSelector();
      } else {
        for (let index = 0; index < buttonElements.length; index++) {
          let buttonElement = buttonElements[index];
          let optionLabelElement = buttonElement.querySelector('.alert-radio-label');
          let color = optionLabelElement.innerHTML.trim();

          if (this.isHexColor(color)) {
            buttonElement.classList.add('colorselect', 'color_' + color.slice(1, 7));
            if (color == this.status.cor) {
              buttonElement.classList.add('colorselected');
            }
          }
        }
      }
    }, 100);
  }

  isHexColor(color) {
    let hexColorRegEx = /^#(?:[0-9a-fA-F]{3}){1,2}$/;
    return hexColorRegEx.test(color);
  }

  selectColor(color) {
    let buttonElements = document.querySelectorAll('div.alert-radio-group button.colorselect');
    for (let index = 0; index < buttonElements.length; index++) {
      let buttonElement = buttonElements[index];
      buttonElement.classList.remove('colorselected');
      if (buttonElement.classList.contains('color_' + color.slice(1, 7))) {
        buttonElement.classList.add('colorselected');
      }
    }
  }

  setColor(value) {
    //this.status.cor = value;
  }

  abrirSelecaoCor(event: any) {

  }

  fecharSelecaoCor(event: any) {

  }
  eventoSelecionarCor(event: any) {
    this.status.cor = event.color;
  }
}

